<template>
    <div class="x-dealView">
        <mt-cell title="借款金额（元）" :value="dealInfo.loanAmount"></mt-cell>
        <mt-cell title="借款日期" :value="dealInfo.loanStartTime"></mt-cell>
        <mt-cell title="还款日期" :value="dealInfo.loanEndTime"></mt-cell>
        <mt-cell title="出借人姓名" :value="dealInfo.name"></mt-cell>
        <mt-cell title="借款人姓名" :value="dealInfo.loanName"></mt-cell>
        <mt-checklist
            v-model="value"
            class="x-cehck-list"
            :options="['已阅读并同意']">
        </mt-checklist>
        <div class="agreement">
            <a href="http://haron.natapp1.cc/loanque.html">《完呗服务借款协议》</a>
            和
            <a href="http://haron.natapp1.cc/serviceque.html">《完呗居间服务协议》</a>
        </div>
        <div class="x-tips">
            <div class="x-tip-title">提示</div>
            <div class="x-tip-text">1、 郑重声明：完呗服务平台不从事任何放货及催收业务，如果有他人以“完呗”名义进行放货及催收，均与“完呗”无关，由此引起的争议完呗服务平台不承担任何责任，完呗服务平台保留追究其法律责任的权利。</div>
            <div class="x-tip-text">2、 逾期和展期的费用由甲方和乙方自行协商确定。</div>
            <div class="x-tip-text">3、 在乙方出具借条后，甲方尚未确认借条前，乙方可以删除借条，终止借款。</div>
            <div class="x-tip-text">4、 出具借条后不予退还交易服务费，以下情况除外：同一张借条重复支付交易服务费，且扣款方在完呗服务平台指定时间内提供重复扣费成功证明。</div>
        </div>
        <mt-button class="x-btn" type="primary" size="large" @click="makeSure">确定</mt-button>
    </div>
</template>
<script>
    import urls from "../action/action-interface.js"
    import ajax from "../action/ajax.js"
    import { Toast } from 'mint-ui'
    import util from "../util"

    export default({
        data () {
            return {
                value: [],
                dealInfo: {
                    loanAmount: '',
                    loanEndTime: '',
                    loanName: '',
                    name: '',
                    loanStartTime: ''
                }
            }
        },
        created () {
            let query = util.parseQueryString();
            ajax.get(urls.qrRead, {
                params: {
                    i: query.id
                }
            }).then((res) => {
                this.dealInfo = res.data.data.data;
            })
        },
        methods: {
            makeSure () {
                let query = util.parseQueryString();
                if (!this.value[0]) {
                    this.toast('请阅读协议')
                    return false;
                }
                ajax.get(urls.qrConfirm, {
                    params: {
                        i: query.id
                    }
                }).then((res) => {
                    if (res.data.code === 0) {
                        Toast({
                            message: '成功',
                            iconClass: 'icon icon-success',
                            className: 'x-toast',
                            duration: 5000
                        })
                    }
                })
            },
            toast (msg) {
                Toast({
                    message: msg,
                    duration: 3000
                })
            }
        }
    })
</script>
<style lang="scss" scoped>
    .x-dealView {
        .agreement {
            position: absolute;
            top: 380px;
            left: 210px;
            height: 76px;
            line-height: 77px;
            font-size: 24px;
            color: #999;
            a {
                text-decoration: none;
                color: #d63a29;
            }
        }
        .x-tips {
            padding: 0 30px;
            font-size: 18px;
            line-height: 150%;
            .x-tip-title {
                color: #777;
                margin-bottom: 8px;
            }
            .x-tip-text {
                color: #d63a29;
            }
        }
        .x-btn {
            margin: 30px auto 0;
            height: 80px;
            font-size: 28px;
            width: 690px
        }
    }
</style>



