<template>
    <div class="x-personal">
        <div class="x-header" @click="goInfo">
            <div class="x-header-img">
                <img :src="headerImg" />
            </div>
            <div class="x-header-info">
                <div class="x-header-name">未实名认证</div>
                <div class="x-header-mobile">
                    <img class="mobile-icon" src="../images/mobile.png" />
                    <div>{{mobile ? mobile : '暂无'}}</div>
                </div>
            </div>
        </div>
        <div class="x-modules">
            <a href="/#/home?type=1" class="x-modules-item x-loan">
                <div>
                    <img class="x-modules-icon" src="../images/in.png" />
                    <div class="x-modules-text">借入</div>
                </div>
            </a>
            <a href="/#/home?type=2" class="x-modules-item x-pay">
                <div>
                    <img class="x-modules-icon" src="../images/out.png" />
                    <div class="x-modules-text">借出</div>
                </div>
            </a>
        </div>
    </div>
</template>
<script>
    import urls from "../action/action-interface.js"
    import ajax from "../action/ajax.js"

    export default({
        data () {
            return {
                headerImg: '',
                mobile: ''
            }
        },
        created () {
            ajax.get(urls.userGetInfo).then((res) => {
                this.headerImg = res.data.data.data.headImgUrl;
                this.mobile = res.data.data.data.mobile;
            })
        },
        methods: {
            goInfo () {
                let url = this.mobile ? '#/baseView' : '#/baseInfo';
                location.href = url;
            }
        }
    })
</script>
<style lang="scss" scoped>
    .x-personal {
        .x-header {
            height: 180px;
            background: #fff;
            display: flex;
            align-items: center;
            .x-header-img {
                width: 100px;
                height: 100px;
                margin-left: 40px;
                img {
                    width: 100%;
                    height: 100%;
                }
            }
            .x-header-info {
                margin-left: 30px;
                .x-header-name {
                    font-size: 26px;
                    color: #333;
                }
                .x-header-mobile {
                    font-size: 24px;
                    color: #777;
                    display: flex;
                    align-items: center;
                    margin-top: 8px;
                    .mobile-icon {
                        height: 28px;
                        height: 28px;
                        margin-right: 8px;
                    }
                }
            }
        }
        .x-modules {
            height: 150px;
            background: #fff;
            margin-top: 30px;
            display: flex;
            .x-modules-item {
                width: 50%;
                height: 100%;
                border-right: 2px solid #dedede;
                display: flex;
                align-items: center;
                justify-content: center;
                text-align: center;
                text-decoration: none;
                .x-modules-icon {
                    width: 76px;
                    height: 76px;
                }
                .x-modules-text {
                    color: #444;
                    font-size: 26px;
                }
            }
            .x-modules-item:last-child {
                border: none;
            }
        }
    }
</style>



