<template>
    <div class="x-baseinfo">
        <mt-field label="姓名" placeholder="请输入姓名" v-model="name"></mt-field>
        <mt-field label="手机号" placeholder="请输入手机号" v-model="mobile"></mt-field>
        <mt-field label="备用手机号1" placeholder="请输入备用手机号1" v-model="sparePhone1"></mt-field>
        <mt-field label="备用手机号2" placeholder="请输入备用手机号2" v-model="sparePhone2"></mt-field>
        <mt-field label="身份证号码" placeholder="请输入身份证号码" v-model="idCard"></mt-field>
        <div class="file-label">请上传身份证正面</div>
        <div class="file-content">
            <img :class="{'preview-img': true, 'active': frontPic}" :src="frontPic" />
            <a href="javascript:void(0)" className="camera">
                <input type="file" name="card-one" @change="changeFile" accept="image/*" />
            </a>
        </div>
        <div class="file-label">请上传身份证反面</div>
        <div class="file-content">
            <img :class="{'preview-img': true, 'active': backPic}" :src="backPic" />
            <a href="javascript:void(0)" className="camera">
                <input type="file" name="card-two" @change="changeFile" accept="image/*" />
            </a>
        </div>
        <div class="file-label">请上传手持身份证</div>
        <div class="file-content">
            <img :class="{'preview-img': true, 'active': handPic}" :src="handPic" />
            <a href="javascript:void(0)" className="camera">
                <input type="file" name="card-three" @change="changeFile" accept="image/*" />
            </a>
        </div>
        <mt-button type="primary" size="large" @click="makeSure">确定</mt-button>
    </div>
</template>
<script>
    import urls from "../action/action-interface.js"
    import ajax from "../action/ajax.js"
    import { Toast } from 'mint-ui'

    export default({
        data () {
            return {
                name: '',
                mobile: '',
                sparePhone1: '',
                sparePhone2: '',
                idCard: '',
                frontPic: '',
                backPic: '',
                handPic: ''
            }
        },
        methods: {
            changeFile (e) {
                window.URL = window.URL || window.webkitURL;
                let file = e.target.files[0]
                // let imgsrc = window.URL.createObjectURL(file);
                let param = new FormData() // 创建form对象
                param.append('file', file) // 通过append向form对象添加数据
                let config = {
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                }
                ajax.post('/file/saveIdcard', param, config).then((res) => {
                    console.log(res.data)
                    let imgUrl = ''
                    if (res.data.code === 0) {
                        imgUrl = res.data.data.data;
                    } else {
                        return false;
                    }
                    switch (e.target.name) {
                        case 'card-one':
                            this.frontPic = imgUrl;
                            break;
                        case 'card-two':
                            this.backPic = imgUrl;
                            break;
                        case 'card-three':
                            this.handPic = imgUrl;
                            break;
                    }
                })
            },
            makeSure () {
                if (!this.name) {
                    this.toast('请输入姓名')
                    return false;
                }
                if (!this.mobile) {
                    this.toast('请输入手机号')
                    return false;
                }
                if (!/^1\d{10}$/.test(this.mobile)) {
                    this.toast('请输入正确的手机号')
                    return false;
                }
                if (!this.sparePhone1) {
                    this.toast('请输入备用手机号1')
                    return false;
                }
                if (!this.sparePhone2) {
                    this.toast('请输入备用手机号2')
                    return false;
                }
                if (!this.idCard) {
                    this.toast('请输入身份证号码')
                    return false;
                }
                if (!this.frontPic) {
                    this.toast('请上传身份证正面')
                    return false;
                }
                if (!this.backPic) {
                    this.toast('请上传身份证反面')
                    return false;
                }
                if (!this.handPic) {
                    this.toast('请上传手持身份证')
                    return false;
                }
                let params = {
                    name: this.name,
                    mobile: this.mobile,
                    sparePhone1: this.sparePhone1,
                    sparePhone2: this.sparePhone2,
                    idCard: this.idCard,
                    frontPic: this.frontPic,
                    backPic: this.backPic,
                    handPic: this.handPic
                }
                ajax({
                    url: urls.userSaveInfo,
                    method: 'POST',
                    headers: { 'content-type': 'application/json' },
                    data: JSON.stringify(params)
                }).then((res) => {
                    if (res.data.code === 0) {
                        Toast({
                            message: '成功',
                            iconClass: 'icon icon-success',
                            duration: 6000
                        })
                    }
                })
            },
            toast (msg) {
                Toast({
                    message: msg,
                    duration: 300000
                })
            }
        }
    })
</script>
<style lang='scss' scoped>
    .x-baseinfo {
        .file-label {
            height: 60px;
            line-height: 60px;
            font-size: 24px;
            color: #666;
            padding-left: 30px;
        }
        .file-content {
            width: 300px;
            height: 300px;
            margin: 0 auto;
            background: url('../images/upload.png') no-repeat;
            background-position: 50% 50%;
            .preview-img {
                position: absolute;
                width: 300px;
                height: 300px;
                opacity: 0;
                border: none;
            }
            .preview-img.active {
                opacity: 1;
            }
            input[type="file"] {
                opacity: 0;
                display: block;
                width: 100%;
                height: 100%;
            }
        }
        .mint-field {
            height: 76px;
        }
        .mint-button--large {
            margin-top: 30px;
            height: 80px;
            border-radius: 0;
            font-size: 28px;
        }
    }
</style>



