<template>
    <div class="x-baseview">
        <mt-cell title="姓名" :value="editInfo.name"></mt-cell>
        <mt-cell title="手机号" :value="editInfo.mobile"></mt-cell>
        <mt-cell title="备用手机号1" :value="editInfo.sparePhone1"></mt-cell>
        <mt-cell title="备用手机号2" :value="editInfo.sparePhone2"></mt-cell>
        <mt-cell title="身份证号码" :value="editInfo.idCard"></mt-cell>
        <div class="file-label">请上传身份证正面</div>
        <div class="file-content">
            <img class="preview-img" :src="editInfo.frontPic" />
        </div>
        <div class="file-label">请上传身份证反面</div>
        <div class="file-content">
            <img class="preview-img" :src="editInfo.backPic" />
        </div>
        <div class="file-label">请上传手持身份证</div>
        <div class="file-content">
            <img class="preview-img" :src="editInfo.handPic" />
        </div>
    </div>
</template>
<script>
    import urls from "../action/action-interface.js"
    import ajax from "../action/ajax.js"

    export default({
        data () {
            return {
                editInfo: {}
            }
        },
        created () {
            ajax.get(urls.userGetInfo).then((res) => {
                this.editInfo = res.data.data.data;
            })
        }
    })
</script>
<style lang='scss' scoped>
    .x-baseview {
        .file-label {
            height: 60px;
            line-height: 60px;
            font-size: 24px;
            color: #666;
            padding-left: 30px;
        }
        .file-content {
            width: 300px;
            height: 300px;
            margin: 0 auto;
            .preview-img {
                position: absolute;
                width: 300px;
                height: 300px;
            }
        }
    }
</style>



