import Vue from 'vue'
import Router from 'vue-router'
import Personal from '@/pages/personal'
import Home from '@/pages/home'
import BaseInfo from '@/pages/baseInfo'
import BaseView from '@/pages/BaseView'
import DealInfo from '@/pages/dealInfo'
import DealView from '@/pages/dealView'
import Index from '@/pages/index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      meta: {
        title: '首页'
      }
    },
    {
      path: '/personal',
      name: 'personal',
      component: Personal,
      meta: {
        title: '个人中心'
      }
    },
    {
      path: '/home',
      name: 'home',
      component: Home,
      meta: {
        title: '首页'
      }
    },
    {
      path: '/baseInfo',
      name: 'baseInfo',
      component: BaseInfo,
      meta: {
        title: '个人信息'
      }
    },
    {
      path: '/baseView',
      name: 'baseView',
      component: BaseView,
      meta: {
        title: '个人信息查看'
      }
    },
    {
      path: '/dealInfo',
      name: 'dealInfo',
      component: DealInfo,
      meta: {
        title: '我是出借人'
      }
    },
    {
      path: '/dealView',
      name: 'dealView',
      component: DealView,
      meta: {
        title: '我是借款人'
      }
    },
    {
      path: '/index',
      name: 'index',
      component: Index,
      meta: {
        title: '导航'
      }
    }
  ]
})



