<template>
    <div class="x-home">
        <div class="x-borrow-list">
            <mt-search v-model="value" placeholder="搜索姓名" :show="true">
                <div class="x-select-group">
                    <div class="x-select-item" @click="popShow('date')">{{dateText}}</div>
                    <div class="x-select-item" @click="popShow('amount')">{{amountText}}</div>
                </div>
                <mt-navbar v-model="selected">
                    <mt-tab-item id="1">正在借款</mt-tab-item>
                    <mt-tab-item id="2">正在出借</mt-tab-item>
                </mt-navbar>
                <div class="x-table-head">
                    <div class="x-table-th" v-if="selected === '1'">出借人</div>
                    <div class="x-table-th" v-if="selected === '2'">借款人</div>
                    <div class="x-table-th">借款金额</div>
                    <div class="x-table-th">时间</div>
                    <div class="x-table-th">状态</div>
                </div>
                <mt-tab-container v-model="selected">
                    <mt-tab-container-item id="1">
                        <div class="x-table-body" v-for="(x, i) in borrowList" :key="i">
                            <div class="x-table-td">{{x.name}}</div>
                            <div class="x-table-td">{{x.loanAmount}}</div>
                            <div class="x-table-td">{{x.createTime}}</div>
                            <div class="x-table-td">{{['借款中','借款完成'][x.loanType]}}</div>
                        </div>
                        <div v-if="borrowList.length == 0" class="none-data-list"></div>
                    </mt-tab-container-item>
                    <mt-tab-container-item id="2">
                        <div class="x-table-body" v-for="(x, i) in loanList" :key="i">
                            <div class="x-table-td">{{x.loanName}}</div>
                            <div class="x-table-td">{{x.loanAmount}}</div>
                            <div class="x-table-td">{{x.createTime}}</div>
                            <div class="x-table-td">{{['借款中','借款完成'][x.loanType]}}</div>
                        </div>
                        <div v-if="loanList.length == 0" class="none-data-list"></div>
                    </mt-tab-container-item>
                </mt-tab-container>
            </mt-search>
        </div>
        <mt-popup v-model="popupVisible" position="bottom" class="mint-popup" @touchmove.prevent>
            <mt-picker :slots="dataList" :itemHeight="56" :visible-item-count="5" :show-toolbar="true"  ref="datepicker" value-key="cName">
                <div @click="handleConfirm" class="sure">确认</div>
            </mt-picker>
        </mt-popup>
        <mt-popup v-model="popupAmountVisible" position="bottom" class="mint-popup" @touchmove.prevent>
            <mt-picker :slots="amountList" :itemHeight="56" :visible-item-count="5" :show-toolbar="true"  ref="amountpicker" value-key="cName">
                <div @click="handleAmountPop" class="sure">确认</div>
            </mt-picker>
        </mt-popup>
    </div>
</template>
<script>
    import urls from "../action/action-interface.js"
    import ajax from "../action/ajax.js"
    import util from "../util"

    export default({
        data () {
            return {
                selected: '1',
                borrowList: [],
                loanList: [],
                value: '',
                popupVisible: false,
                dateText: '还款日期排序',
                amountText: '状态',
                dataList: [
                    {
                        flex: 1,
                        values: ['还款日期升序', '还款日期降序'],
                        className: 'slot1',
                        textAlign: 'center'
                    }
                ],
                popupAmountVisible: false,
                amountList: [
                    {
                        flex: 1,
                        values: ['出借中', '出借完成'],
                        className: 'slot2',
                        textAlign: 'center'
                    }
                ]
            }
        },
        created () {
            let query = util.parseQueryString()
            this.selected = query.type ? query.type : 1;
        },
        watch: {
            value: function (el) {
                this.getList();
            },
            selected: function (el) {
                this.getList();
            }
        },
        methods: {
            popShow (type) {
                if (type === 'date') {
                    this.popupVisible ? this.popupVisible = false : this.popupVisible = true;
                } else {
                    this.popupAmountVisible ? this.popupAmountVisible = false : this.popupAmountVisible = true;
                }
            },
            handleConfirm (e) {
                this.dateText = this.$refs.datepicker.getValues()[0];
                this.popupVisible = false;
                this.getList();
            },
            handleAmountPop (e) {
                this.amountText = this.$refs.amountpicker.getValues()[0];
                this.popupAmountVisible = false;
                this.getList();
            },
            getList () {
                let params = {
                    loanType: this.$refs.amountpicker.getValues()[0] === '出借中' ? 0 : 1,
                    order: this.$refs.datepicker.getValues()[0] === '还款日期降序' ? 'asc' : 'desc',
                    type: this.selected === "1" ? 1 : 0,
                    page: 1,
                    size: 20
                };
                this.selected === "1" ? params.loanName = this.value : params.name = this.value;
                ajax({
                    url: urls.loanList,
                    method: 'POST',
                    headers: { 'content-type': 'application/json' },
                    data: JSON.stringify(params)
                }).then((res) => {
                    this.selected === "1" ? this.borrowList = res.data.data.data : this.loanList = res.data.data.data;
                })
            }
        }
    })
</script>
<style lang="scss" scoped>
    .x-home {
        .mint-navbar {
            margin-top: 18px;
        }
        .x-amount {
            font-family: DINAlternate-Bold;
        }
        .x-amount-text {
            font-size: 22px;
            margin-top: 4px;
        }
        .mint-navbar .mint-tab-item, .mint-tab-item .mint-tab-item-label {
            font-size: 26px !important;
        }
        .mint-tab-container, .mint-search {
            margin-top: 2px;
        }
        .none-data-list {
            display: block;
            margin: 260px auto;
            width: 128px;
            height: 128px;
            background: url('../images/no-data.png') no-repeat;
        }
        .x-select-group {
            margin-top: 36px;
            height: 60px;
            background-color: #fff;
            display: flex;
            font-size: 24px;
            color: #208fee;
            align-items: center;
            justify-content: center;
            .x-select-item {
                width: 50%;
                text-align: center;
            }
            .x-select-item:first-child {
                border-right: 2px solid #dedede;
            }
        }
        .mint-popup {
            width: 100%;
        }
        .sure {
            position:absolute;
            right: 16px;
            width: 60px;
            height: 60px;
            font-size: 26px;
            line-height: 60px;
            text-align: center;
            border: none;
            background: none;
            box-shadow: none;
            color: #208fee;
        }
        .x-table-head {
            margin-top: 20px;
        }
        .x-table-body, .x-table-head {
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            text-align: center;
            height: 60px;
            background-color: #fff;
            font-size: 24px;
        }
        .x-table-body {
            border-bottom: 1px solid #dedede;
        }
        .x-table-th, .x-table-td {
            width: 25%;
        }
    }
</style>



