import { Toast } from 'mint-ui'
import axios from 'axios'

axios.defaults.baseURL = "http://haron.natapp1.cc/"
axios.defaults.withCredentials = true
axios.defaults.timeout = 30000; // 延时设置
axios.interceptors.response.use((res) => { // code状态码-200002判断
    if (res.data.code < 0) {
        Toast({
            message: '服务器正忙，请稍后再试',
            duration: 5000
        });
    } else if (res.data.code > 0) {
        Toast({
            message: res.data.message || '服务器正忙，请稍后再试',
            duration: 5000
        });
    }
    return res
}, (error) => {
    return Promise.reject(error)
});

export default axios



